import React from "react";

function FileDownloadButton() {
  const fileUrl = "/img/"; // Replace with the actual path to your file
  const imagePath = `${fileUrl}poolListingThumbnail.png`;
  const handleDownload = () => {
    // Create a temporary anchor element
    const anchor = document.createElement("a");
    anchor.href = fileUrl;
    anchor.download = "Elite-Splash--BR-Pool-Models.pdf"; // Replace with the desired filename and extension

    // Programmatically click the anchor element to trigger the download
    anchor.click();

    // Clean up the temporary anchor element
    URL.revokeObjectURL(anchor.href);
  };

  return (
    <div>
      <p>{imagePath}</p>
      < img src={imagePath} />
      <button
        className="text-white bg-teal hover:bg-teal-secondary font-bold py-2 px-4 rounded-full m-auto my-0"
        onClick={handleDownload}
      >
        Download Pool Models
      </button>
    </div>
  );
}

export default FileDownloadButton;
